import "./App.css";
import { useState, useEffect } from "react";

const axios = require("axios");

const App = () => {
  const [posts, setPosts] = useState([]);
  const [page, setPage] = useState(0);

  useEffect(() => {
    console.log("hello");
    axios.get(`/posts/${page}`).then((res) => setPosts(res.data));
  }, [page]);

  return (
    <div className="App">
      {posts.map((post) => post.attachments.map((url) => <img src={url} />))}
      <button onClick={() => setPage(page + 1)}>Next page</button>
      <button
        onClick={() => {
          if (page !== 0) {
            setPage(page - 1);
          }
        }}
      >
        Prev page
      </button>
    </div>
  );
};

export default App;
